.stars {
  display: flex;

  &__star {
    background-image: url(../images/star.svg) no-repeat center;
    height: 16px;
    width: 16px;
    margin-right: 4px;
  }

  &__star:last-child {
    margin-right: 0;
  }

  &__star:nth-child(-n+0),
  &__star:nth-child(-n+1),
  &__star:nth-child(-n+2),
  &__star:nth-child(-n+3),
  &__star:nth-child(-n+4) {
    background-image: url(../images/star-active.svg);
  }
}
